<template>
  <div>
    <van-nav-bar
      title="调度信息填报"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="Content border-radius">
      <div class="containerMain" style="top:0">
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度时间：</span>
          </div>
          <div class="select" @click="selectStartTime()">
            <input type="text" class="input" disabled v-model="Time" placeholder="选择调度时间" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度地点：</span>
          </div>
          <div class="select" id="selectAdress" @click="selectAdress()  ">
            <input type="text" class="input" disabled v-model="adrees" placeholder="选择调度地点" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>详细地址：</span>
          </div>
          <div class="select">
            <input type="text" class="input" v-model="adreesTo" placeholder="填写详细地址" />
          </div>
        </div>
      </div>
      <div class="containerMain" style="top:0">
        <div class="flexContainer">
          <div class="titleselect">
            <span>调度车辆：</span>
          </div>
          <!-- @click="selectCar() " -->
          <div class="select" id="selectCarid">
            <input type="text" class="input" disabled v-model="Car" placeholder="选择调度车辆" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>车牌号：</span>
          </div>
          <div class="select" id>
            <input type="text" class="input" disabled v-model="vehicle" placeholder="选择调度车牌号" />
          </div>
        </div>
      </div>

      <!-- <van-cell-group>
        <van-field v-model="value" placeholder="请输入用户名" />
      </van-cell-group>-->

      <div
        class="submit backgroundNaviBar"
        :style="'background:'+$store.state.color"
        @click="submitBefore"
      >
        <span>提交</span>
      </div>
    </div>

    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <!-- <mu-sub-header>时间筛选</mu-sub-header> -->
          <van-datetime-picker
            v-model="currentDate"
            type="datetime"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="confirmTime"
            @cancel="cancel"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Adress from "../../../static/js/adress";
import Vue from "vue";
import { Toast } from "vant";
import { Overlay, Loading } from "vant";
import { Dialog } from "vant";
// import Vue from 'vue';
import { Field } from "vant";

Vue.use(Field);
Vue.use(Toast);
Vue.use(Overlay, Loading);
export default {
  data() {
    return {
      showover: false,
      Time: "",
      adrees: "",
      AdressArr: [],
      Car: "VE001",
      vehicle: "粤B123156",
      token: "",
      minDate: new Date("1970-07-01"),
      maxDate: new Date("2070-07-01"),
      currentDate: new Date(),
      open: false,
      adreesTo: "",
      value: ""
    };
  },
  methods: {
    cancel() {
      this.open = false;
    },
    confirmTime(value) {
      this.Time = AppBace.filterTime(value);
      this.open = false;
    },
    closeBottomSheet() {
      this.open = false;
    },
    submitBefore() {
      var _this = this;
      Dialog.confirm({
        title: "提交",
        message: "确认提交？"
      })
        .then(() => {
          // on confirm
          _this.submit();
          _this.showover = true;
        })
        .catch(() => {
          // on cancel
        });
    },
    async submit() {
      var url = "/job/dispatch";
      var address = this.adrees + this.adreesTo;
      var time = this.Time;
      var vehicle = this.Car;
      if (address==""||time==""||vehicle=="") {
          Toast.fail("请完善信息！");
          this.showover = false;
      } else {
        var res = await AppBace.request(
          url,
          "POST",
          {
            address,
            time,
            vehicle
          },
          this.token
        );
        console.log(res);
        if (res.data.message === "success") {
          Toast("新增成功");
          this.showover = false;
          this.$router.push({
            path: "/Navigation",
            query: {
              id: 1
            }
          });
        } else {
          Toast("新增失败,请重试！");
          this.showover = false;
        }
      }
    },
    selectStartTime() {
      this.open = true;
    },
    selectAdress() {
      //   this.open = false;

      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectAdress, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.AdressArr, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择车辆", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr);
          _this.adrees = selectArr[0] + selectArr[1] + selectArr[2];
        }
      });
    },
    selectCar() {
      //   this.open = false;

      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectCarid, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        // data: this.AdressArr, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        data: this.$store.state.vehicleList, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择车辆", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr);
          _this.Car = selectArr[0];
          switch (selectArr[0]) {
            case "VE001":
              _this.vehicle = "粤B123156";
              break;
            case "VE002":
              _this.vehicle = "粤B123156";
              break;
            default:
              _this.vehicle = "粤B123156";
              break;
          }
          //   _this.open = true;
          // var name = Echarts.switchData(selectArr[0]);
          //   _this.initData(name);
          // console.log(JSON.stringify(selectArr))
        }
      });
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.AdressArr = Adress.AdressData();
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/maintain.css";
.submit {
  background: rgb(78, 217, 149);
  color: white;
  border-radius: 10px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  margin-top: 20px;
}
.Content {
  padding: 10px;
}
/* @import "../../../static/css/selectCommon.css"; */
</style>


